body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.truckimg {
    width: 40px;
    height: 60px;
}

.treasureimg {
    width: 50px;
    height: 40px;
}

.blip {
    width: 40px;
    height: 30px;
}

.overmap {
    width: 100%;
    position: absolute;
    bottom: 90px;
    text-align: center;
    z-index: 1000;
}

.endroutebtn {
  z-index: 1000;
  width: 80%;
  height: 50px;
  font-weight: bolder;
  color: white;
  font-size: 20px;
  border: 2px solid black;
  border-radius: 10px;
}

.confirmbtn {
    z-index: 1000;
    width: 80%;
    height: 50px;
    font-weight: bolder;
    color: white;
    font-size: 20px;
    border: 2px solid black;
    border-radius: 10px;
}

.directionp {
    z-index: 1000;
    width: 80%;
    font-weight: bolder;
    background-color: white;
    color: black;
    font-size: 20px;
    margin: 0 auto;
    padding: 8px;
    border: 2px solid black;
    border-radius: 10px;
}

.mapboxgl-ctrl-top-right {
    width: 10%;
}
.mapboxgl-ctrl-top-left {
    width: 10%;
}

@media only screen and (min-width: 768px) {
    .mapboxgl-ctrl-top-right {
        width: 50%;
    }
    .mapboxgl-ctrl-top-left {
        width: 50%;
    }
}



.bottomContainer {
    position: absolute;
    bottom: 2px;
    width: 100%;
    height: 80px;
    background: #2A2E43;
    border-radius: 10px 10px 0px 0px;
    display: block;
    margin: 0 auto;
    border-top: 4px solid #ec008c;
    -webkit-transition: height .7s ease-in;
    transition: height .7s ease-in;
    z-index: 1001!important;
  }

  .openBottom {
    height: 360px;
  }

  .opening {
    color: #fff;
    cursor: pointer;
  }

  .middle {
    padding-top: 20px;
    padding-left: 10px;
    display: inline-block;
  }

  .middle h3 {
    margin: 0;
  }

  .middle span {
    color: #9a9ca6;
  }

  .fa-angle-up {
    vertical-align: bottom;
    color: #9a9ca6;
    display: inline-block;
    font-size: 30px;
  }

  .rotate {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
    -webkit-transition: -webkit-transform 400ms ease;
    transition: -webkit-transform 400ms ease;
    transition: transform 400ms ease;
    transition: transform 400ms ease, -webkit-transform 400ms ease;
  }

  .outerContianer {
    width: 80%;
    display: block;
    margin: 0 auto;
  }

  .bottomOuterContainer {
    max-width: 400px;
    padding-top: 30px;
    display: none;
    margin: 0 auto;
  }

  .bottomInnercontainer {
    display: flex;
    flex-wrap: wrap;
  }

  .bottomInnercontainer>div {
    flex-grow: 1;
    width: 33%;
    height: 100px;
    margin: 10px 0px;
  }

  .bottomButton {
    display: block;
    margin: 0 auto;
    width: 90px;
    height: 100px;
    border-radius: 12px;
    border: none;
    background-color: #353A50;
    color: #fff;
    cursor: pointer;
  }

  .fa-truck-pickup {
    width: 100px;
  }

  .bold {
    font-weight: bold;
    font-size: 1.2em;
  }

  .bottomButton:hover {
    background-color: #454F63;
  }

  .display {
    display: flex !important;
  }
